import React, { Component } from "react";
import axios from "axios";


class Jokes extends Component {
  constructor(props) {
    super(props);
    this.state = {value: '',
    patchid: '',
    screen: 'GET',
    posts: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleIdChange = this.handleIdChange.bind(this);
    this.handleGetSubmit = this.handleGetSubmit.bind(this);
    this.handlePostSubmit = this.handlePostSubmit.bind(this);
    this.handlePatchSubmit = this.handlePatchSubmit.bind(this);
    this.handleDeleteSubmit = this.handleDeleteSubmit.bind(this);
  }

  handleButtonClick(page) {
    this.setState({ screen: page });
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleIdChange(event) {
    this.setState({patchid: event.target.value})
  }

  handleGetSubmit(event) {
    event.preventDefault();
    axios.get('https://cors-anywhere.herokuapp.com/https://personal-api-e8892.web.app/api/v1/jokes')
    .then((response) => {
      const obj = response.data.jokes;
       var result = [];
       for (const subs in obj) {
         var value = obj[subs]
         result.push(value);
       }
      this.setState({posts: JSON.stringify(result,null,'\t')});
    })
  .catch(function (error) {
    console.log(error);
  });

  }
  //   ({
  //     method: 'post',
  //     url: '/api/v1/jokes',
  //     data: {
  //       joke: this.state.value
  //     }
  //   });
  // }

  handlePostSubmit(event) {
    event.preventDefault();
    axios.post('https://cors-anywhere.herokuapp.com/https://personal-api-e8892.web.app/api/v1/jokes', {
      joke: this.state.value
    })
    .then((response) => {
      const obj = response.data;
      this.setState({posts: JSON.stringify(obj,null,'\t')});
      const docid = this.state.posts.substring(21,41);
      this.setState({patchid: docid});
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  handlePatchSubmit(event) {
    event.preventDefault();
    const temppatchid = this.state.patchid
    const patchurl = "https://cors-anywhere.herokuapp.com/https://personal-api-e8892.web.app/api/v1/jokes/" + temppatchid
    axios.patch(patchurl, {
      joke: this.state.value
    })
    .then((response) => {
      const obj = this.state.patchid + " successfuly changed";
      this.setState({posts: JSON.stringify(obj,null,'\t')});
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  handleDeleteSubmit(event) {
    event.preventDefault();
    const temppatchid = this.state.patchid
    const patchurl = "https://cors-anywhere.herokuapp.com/https://personal-api-e8892.web.app/api/v1/jokes/" + temppatchid
    axios.delete(patchurl)
    .then((response) => {
      const obj = response.data;
      this.setState({posts: JSON.stringify(obj,null,'\t')});
      const docid = "";
      this.setState({patchid: docid});
      this.setState({value: docid});
    })
    .catch(function (error) {
      console.log(error);
    });
  }



  render() {
    var screenvalue = this.state.screen;
    let comp;

    if (screenvalue === 'GET') {
    comp =  <div>
      <form onSubmit={this.handleGetSubmit}>
      <button type="submit" value="Submit">Submit</button>
      </form>
      </div>
    } else if (screenvalue === "POST") {
      comp = <div>
      <form onSubmit={this.handlePostSubmit}>
      <label>
        Joke:
        <input type="text" value={this.state.value} onChange={this.handleChange} />
      </label>
      <button type="submit" value="Submit">Submit</button>
      </form>
      </div>
    } else if (screenvalue === "PATCH") {
      comp = <div>
      <form onSubmit={this.handlePatchSubmit}>
      <label>
        Joke ID:
        <input type="text" value={this.state.patchid} onChange={this.handleIdChange} />
      </label>
      <label>
        Joke:
        <input type="text" value={this.state.value} onChange={this.handleChange} />
      </label>
      <button type="submit" value="Submit">Submit</button>
      </form>
      </div>
    } else {
      comp = <div>
      <form onSubmit={this.handleDeleteSubmit}>
      <label>
        Joke ID:
        <input type="text" value={this.state.patchid} onChange={this.handleIdChange} />
      </label>
      <button type="submit" value="Submit">Submit</button>
      </form>
      </div>
    }
    return (
      <div className='request'>
      <div className='buttons'>
      <button onClick={(e) => this.handleButtonClick(e.target.value)} value='GET' className={ this.state.screen === "GET" ? 'button-active' : 'button-normal' }>GET</button>
      <button onClick={(e) => this.handleButtonClick(e.target.value)} value='POST' className={ this.state.screen === "POST" ? 'button-active' : 'button-normal' }>POST</button>
      <button onClick={(e) => this.handleButtonClick(e.target.value)} value='PATCH' className={ this.state.screen === "PATCH" ? 'button-active' : 'button-normal' }>PATCH</button>
      <button onClick={(e) => this.handleButtonClick(e.target.value)} value='DELETE' className={ this.state.screen === "DELETE" ? 'button-active' : 'button-normal' }>DELETE</button>
      </div>
      <div className='formbox'>
      <div className='form'>
      {comp}
      </div>
      <div className="returnbox">
      <pre className="box">{this.state.posts}</pre>
      </div>
      </div>
      </div>
    );
  }
}
export default Jokes;
