import React, { Component } from "react";
import ExperiencesGet from "./API/ExperiencesGet";
import FunFactsGet from "./API/FunFactsGet";
import InterestsGet from "./API/InterestsGet";
import Jokes from "./API/Jokes";
import Feedback from "./API/Feedback";
import ContactAPI from "./API/ContactAPI";
import Collapse from 'react-bootstrap/Collapse';
import MediaQuery from "react-responsive";

class Api extends Component {
  constructor(props){
    super(props);
    this.state = {value: '/v1/work'};

    this.handleChange = this.handleChange.bind(this);
  }


  handleChange(event) {
    this.setState({value: event.target.value});
  }


  render() {
    return <div className="api-section" id="api">
    <MediaQuery query='(max-device-width: 409px)'>
    <div className="apiheader" data-aos="zoom-in">&lt; API /&gt;</div>
    </MediaQuery>
    <MediaQuery query='(min-device-width: 410px)'>
    <div className="apiheader" data-aos="zoom-in">&lt; Personal API /&gt;</div>
    </MediaQuery>
    <br /><br />
    <div className="apiselect">
    <select className="select-css" value={this.state.value} onChange={this.handleChange}>
        <option>/v1/work</option>
        <option>/v1/interests</option>
        <option>/v1/fun-facts</option>
        <option>/v1/jokes</option>
        <option>/v1/feedback</option>
        <option>/v1/contact-me</option>
    </select><div className="arrow">▼</div>

    </div>
    <br />
    <br />
    <div>
      {(() => {
        if (this.state.value == "/v1/work") {
          return (<ExperiencesGet />)
        } else if (this.state.value == "/v1/interests") {
          return (<InterestsGet />)
        } else if (this.state.value == "/v1/fun-facts") {
          return (<FunFactsGet />)
        } else if (this.state.value == "/v1/jokes") {
          return (<Jokes />)
        } else if (this.state.value == "/v1/feedback") {
            return (<Feedback />)
        } else {
          return (<ContactAPI />)}
      })()}
    </div>



    </div>
  }
}

export default Api;
