import React, { Component } from "react";
import ProjectBorder from "../components/ProjectBorder";
import webimage from './projects/webimage.png';
import Dialog from '@material-ui/core/Dialog';
import Slide from '@material-ui/core/Slide';
import MediaQuery from "react-responsive";

import WebsiteProject from "./projects/WebsiteProject";
import Stock from "./projects/Stock";
import Language from "./projects/Language";
import Praan from "./projects/Praan";
import ApiProject from "./projects/ApiProject";
import stockimage from './projects/stockimage.png';
import languagepic from './projects/languagepic.png';
import praanpic from './projects/praan.png';
import apipic from './projects/apipic.png';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="left" ref={ref} {...props} />;
});


class Projects extends Component {
  constructor(props){
    super(props);
    this.state = {
      open: false,
      screenlogic: 0,
    }
    this.handleOpen = this.handleOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleOpen(e, value) {
    if (value == "1") {
      this.setState({screenlogic: 1})
    } else if (value == "2") {
      this.setState({screenlogic: 2})
    } else if (value == "3") {
      this.setState({screenlogic: 3})
    } else if (value == "4") {
      this.setState({screenlogic: 4})
    } else {
      this.setState({screenlogic: 5})
    }
    this.setState({open: true});
  }

  handleClose() {
    this.setState({open: false});
  }

  render() {
    const imageStyle = {
      width: "100%",
      boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2)",
      borderBottomLeftRadius: "10px",
      borderBottomRightRadius: "10px"
    }

    const borderStyle = {
        maxWidth: "100%",
        height: "30px",
        borderTopLeftRadius: "10px",
        borderTopRightRadius: "10px",
        paddingTop: "2%",
        paddingLeft: "2%",
        cursor: "default",
        boxShadow: "1px 4px 8px 1px rgba(0, 0, 0, 0.2)"
    }

    return (
    <div className="projects-section" id="projects">
    <div className="projectHolderLeft">
    <div className="projectleft" data-aos="fade-up" data-aos-duration="1000" onClick={(e)=>this.handleOpen(e, "2")}>
      <h1 style={{textAlign: "center"}}> Language App </h1>
      <ProjectBorder />
      <img src={languagepic} style={imageStyle} />
    </div>


    <div className="projectleft" data-aos="fade-up" data-aos-duration="1000" onClick={(e)=>this.handleOpen(e, "5")}>
    <h1 style={{textAlign: "center"}}> Praan.io Website </h1>
      <ProjectBorder />
      <img src={praanpic} style={imageStyle} />
    </div>


      </div>


      <div className="projectHolderRight" >
      <div className="projectright" data-aos="fade-up" data-aos-duration="1000" onClick={(e)=>this.handleOpen(e, "4")}>
      <h1 style={{textAlign: "center"}}> Personal API </h1>
        <ProjectBorder />
        <img src={apipic} style={imageStyle} />
      </div>

      <div className="projectright" data-aos="fade-up" data-aos-duration="1000" onClick={(e)=>this.handleOpen(e, "1")}>

        <h1 style={{textAlign: "center"}}> Penny Stocks App </h1>
        <ProjectBorder style={borderStyle}/>
        <img src={stockimage} style={imageStyle} />
      </div>

      <div className="projectright" data-aos="fade-up" data-aos-duration="1000" onClick={(e)=>this.handleOpen(e, "3")}>
      <h1 style={{textAlign: "center"}}> This Website </h1>
      <div className="projectwhole">
        <ProjectBorder />
        <img src={webimage} style={imageStyle} />
        </div>
      </div>
      <br /> <br />

      </div>
      <Dialog fullScreen open={this.state.open} onClose={this.state.open} TransitionComponent={Transition} scroll="body">
      <div>
        {(() => {
          if (this.state.screenlogic == "1") {
            return (<div style={{display: "flex", backgroundColor:"white"}}>
              <button className="close" onClick={(e)=>this.handleClose(e)}>X</button>
              <Stock />
            </div>)
          } else if (this.state.screenlogic == "2") {
            return (<div style={{display: "flex", backgroundColor:"white"}}>
              <button className="close" onClick={(e)=>this.handleClose(e)}>X</button>
              <Language />
            </div>)
          } else if (this.state.screenlogic == "3") {
            return (<div style={{display: "flex", backgroundColor:"white"}}>
              <button className="close" onClick={(e)=>this.handleClose(e)}>X</button>
              <WebsiteProject />
            </div>)
          } else if (this.state.screenlogic == "4") {
            return (<div style={{display: "flex", backgroundColor:"white"}}>
              <button className="close" onClick={(e)=>this.handleClose(e)}>X</button>
              <ApiProject />
            </div>)
          } else {
            return (<div style={{display: "flex", backgroundColor:"white"}}>
              <button className="close" onClick={(e)=>this.handleClose(e)}>X</button>
              <Praan />
            </div>)
          }
        })()}
      </div>
      </Dialog>
    </div>
  );
  }
}

export default Projects;
