import React, { Component } from "react";
import axios from "axios";


class FunFactsGet extends Component {
  constructor(props) {
    super(props);
    this.state = {value: '',
    posts: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }



  handleChange(event) {
    this.setState({value: event.target.value});
  }


  handleSubmit(event) {
    event.preventDefault();
    axios.get('https://cors-anywhere.herokuapp.com/https://personal-api-e8892.web.app/api/v1/funfacts')
    .then((response) => {
      const obj = response.data.funfacts;
       var result = [];
       for (const subs in obj) {
         var value = obj[subs]
         result.push(value);
       }
      this.setState({posts: JSON.stringify(result,null,'\t')});
    })
  .catch(function (error) {
    console.log(error);
  });

  }



  render() {
    return (
      <div className='request'>
      <div className='buttons'>
      <div className='button-active'>GET</div>
      <div className='button-disabled'>POST</div>
      <div className='button-disabled'>PATCH</div>
      <div className='button-disabled'>DELETE</div>
      </div>
      <div className='formbox'>
        <div className='form'>
      <form onSubmit={this.handleSubmit}>
      <button type="submit" value="Submit">Submit</button>
      </form>
      </div>
        <div className="returnbox">
        <pre className="box">{this.state.posts}</pre>
        </div>
        </div>
        </div>
    );
  }
}
export default FunFactsGet;
